import React, { useState, useContext } from "react";
import { styled } from "@mui/material/styles";
import InputBase from "@mui/material/InputBase";
import FiberManualRecordIcon from "@mui/icons-material/FiberManualRecord";
import InputAdornment from "@mui/material/InputAdornment";
import OutlinedInput from "@mui/material/OutlinedInput";
import InputLabel from "@mui/material/InputLabel";
import FormControl from "@mui/material/FormControl";
import Visibility from "@mui/icons-material/Visibility";
import VisibilityOff from "@mui/icons-material/VisibilityOff";
import IconButton from "@mui/material/IconButton";
import Button from "@mui/material/Button";
import { useToasts } from "react-toast-notifications";
import { useHistory } from "react-router-dom";
import UserContext from "../../context/UserContext";
import Axios from "axios";
import { useDispatch } from "react-redux";
import * as Actions from "../../redux/action";
import "./index.css";

const BootstrapInput = styled(InputBase)(({ theme }) => ({
  "label + &": {
    marginTop: theme.spacing(3),
  },
  "& .MuiInputBase-input": {
    position: "relative",
    backgroundColor: "white",
    border: "1px solid #D9D9D6",
    fontSize: 14,
    width: "100%",
    padding: "12px 16px",
    transition: theme.transitions.create([
      "border-color",
      "background-color",
      "box-shadow",
      "border-width",
    ]),
    "&:focus": {
      borderColor: "#FA4616",
      borderWidth: "1px",
    },
  },
}));

export default function Login() {
  const dispatch = useDispatch();
  const { addToast } = useToasts();
  const { loginStatus, setLoginStatus } = useContext(UserContext);
  const history = useHistory();
  const [email, setEmail] = useState("");
  const [values, setValues] = React.useState({
    password: "",
    showPassword: false,
  });

  const handleChange = (prop) => (event) => {
    setValues({ ...values, [prop]: event.target.value });
  };

  const handleClickShowPassword = () => {
    setValues({
      ...values,
      showPassword: !values.showPassword,
    });
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  const login = async () => {
    const password = values.password;

    // Store current user's email to redux
    const payload = {
      email: email,
      customerId: "",
    };
    dispatch(Actions.saveEmail(payload));

    // When user is super admin, redirect to admin page.
    if (email === "superadmin" && password === "superadmin") {
      setLoginStatus(true);
      history.push("/admin");
      return;
    }

    if (!email) {
      addToast("Please Enter Email", {
        appearance: "error",
        autoDismiss: true,
      });
      return;
    }

    if (!password) {
      addToast("Please Enter Password", {
        appearance: "error",
        autoDismiss: true,
      });
      return;
    }

    const loginUser = { email, password };
    const loginRes = await Axios.post(
      "http://localhost:5000/users/login",
      loginUser
    );

    if (loginRes.data.status === "No User") {
      addToast(loginRes.data.msg, {
        appearance: "error",
        autoDismiss: true,
      });
      return;
    }

    if (loginRes.data.status === "Wrong Password") {
      addToast(loginRes.data.msg, {
        appearance: "error",
        autoDismiss: true,
      });
      return;
    }

    if (loginRes.data.status === "No Active") {
      addToast(loginRes.data.msg, {
        appearance: "error",
        autoDismiss: true,
      });
      return;
    }

    // If login is success.
    if (loginRes.data.status === "Success") {
      setLoginStatus(true);
      history.push("/customer");
    }
  };

  return (
    <div className="login">
      <div className="top-bar"></div>
      <p className="title">Sign In</p>
      <div className="reg-item">
        <BootstrapInput
          id="bootstrap-input"
          placeholder="Email"
          sx={{ width: "100%", marginBottom: "14px" }}
          value={email}
          onChange={(e) => setEmail(e.target.value)}
        />
        <FormControl
          sx={{ width: "100%", marginBottom: "14px" }}
          variant="outlined"
        >
          <InputLabel htmlFor="outlined-adornment-password">
            Password
          </InputLabel>
          <OutlinedInput
            id="outlined-adornment-password"
            type={values.showPassword ? "text" : "password"}
            value={values.password}
            onChange={handleChange("password")}
            endAdornment={
              <InputAdornment position="end">
                <IconButton
                  aria-label="toggle password visibility"
                  onClick={handleClickShowPassword}
                  onMouseDown={handleMouseDownPassword}
                  edge="end"
                >
                  {values.showPassword ? <VisibilityOff /> : <Visibility />}
                </IconButton>
              </InputAdornment>
            }
            label="Password"
          />
        </FormControl>
        <Button variant="contained" onClick={login}>
          Login
        </Button>
      </div>
    </div>
  );
}
