import { useParams } from "react-router-dom";
import React, { useState, useEffect } from "react";
import { styled } from "@mui/material/styles";
import InputBase from "@mui/material/InputBase";
import FiberManualRecordIcon from "@mui/icons-material/FiberManualRecord";
import InputAdornment from "@mui/material/InputAdornment";
import OutlinedInput from "@mui/material/OutlinedInput";
import InputLabel from "@mui/material/InputLabel";
import FormControl from "@mui/material/FormControl";
import Visibility from "@mui/icons-material/Visibility";
import VisibilityOff from "@mui/icons-material/VisibilityOff";
import IconButton from "@mui/material/IconButton";
import Button from "@mui/material/Button";
import Axios from "axios";
import { useToasts } from "react-toast-notifications";
import { useHistory } from "react-router-dom";
import Grid from "@mui/material/Grid";
import Select from "@mui/material/Select";
import MenuItem from "@mui/material/MenuItem";
import "./index.css";

const BootstrapInput = styled(InputBase)(({ theme }) => ({
  "label + &": {
    marginTop: theme.spacing(3),
  },
  "& .MuiInputBase-input": {
    position: "relative",
    backgroundColor: "white",
    border: "1px solid #D9D9D6",
    fontSize: 14,
    width: "100%",
    padding: "12px 16px",
    transition: theme.transitions.create([
      "border-color",
      "background-color",
      "box-shadow",
      "border-width",
    ]),
    "&:focus": {
      borderColor: "#FA4616",
      borderWidth: "1px",
    },
  },
}));

export default function Edit() {
  let { id } = useParams();

  const [name, setName] = useState("");
  const [ruc, setRuc] = useState("");
  const [branch, setBranch] = useState("");
  const [token, setToken] = useState("");
  const [email, setEmail] = useState("");
  const [credit, setCredit] = useState("");
  const [vendor, setVendor] = useState("");
  const [process, setProcess] = useState("");
  const [paid, setPaid] = useState("");
  const { addToast } = useToasts();
  const history = useHistory();

  useEffect(() => {
    Axios.get("http://localhost:5000/users/get/" + id).then(
      async (response) => {
        setName(response.data.name);
        setRuc(response.data.ruc);
        setBranch(response.data.branch);
        setToken(response.data.token);
        setCredit(response.data.credit);
        setVendor(response.data.vendor);
        setProcess(response.data.process);
        setPaid(response.data.paid);
        setEmail(response.data.email);
      }
    );
  }, []);

  const submit = async () => {
    const newUser = {
      id,
      name,
      ruc,
      branch,
      token,
      email,
      vendor,
      process,
      credit,
      paid,
    };
    const resultRegister = await Axios.put(
      "http://localhost:5000/users/update",
      newUser
    );

    // Register Success Case
    if (resultRegister.status === 200) {
      history.push("/admin");
    } else {
      addToast(resultRegister.data.msg, {
        appearance: "error",
        autoDismiss: true,
      });
    }
  };
  return (
    <div className="edit">
      <h1>Edit USER</h1>
      <BootstrapInput
        id="bootstrap-input"
        placeholder="Name"
        value={name}
        onChange={(e) => setName(e.target.value)}
        sx={{ width: "100%", marginBottom: "14px" }}
      />
      <Grid container spacing={2}>
        <Grid item xs={6}>
          <BootstrapInput
            id="bootstrap-input"
            placeholder="Ruc"
            value={ruc}
            onChange={(e) => setRuc(e.target.value)}
            sx={{ width: "100%", marginBottom: "14px" }}
          />
        </Grid>
        <Grid item xs={6}>
          <BootstrapInput
            id="bootstrap-input"
            placeholder="Id Sucursal"
            value={branch}
            onChange={(e) => setBranch(e.target.value)}
            sx={{ width: "100%", marginBottom: "14px" }}
          />
        </Grid>
      </Grid>
      <BootstrapInput
        id="bootstrap-input"
        placeholder="Token"
        value={token}
        onChange={(e) => setToken(e.target.value)}
        sx={{ width: "100%", marginBottom: "14px" }}
      />
      <BootstrapInput
        id="bootstrap-input"
        placeholder="Email"
        value={email}
        onChange={(e) => setEmail(e.target.value)}
        sx={{ width: "100%", marginBottom: "14px" }}
      />
      <Grid container spacing={2}>
        <Grid item xs={6}>
          <BootstrapInput
            id="bootstrap-input"
            placeholder="Id Vendor"
            value={vendor}
            onChange={(e) => setVendor(e.target.value)}
            sx={{ width: "100%", marginBottom: "14px" }}
          />
        </Grid>
        <Grid item xs={6}>
          <FormControl sx={{ minWidth: 120 }}>
            <InputLabel id="demo-simple-select-helper-label">
              Tipo Proceso
            </InputLabel>
            <Select
              value={process}
              onChange={(e) => setProcess(e.target.value)}
              displayEmpty
              inputProps={{ "aria-label": "Without label" }}
            >
              <MenuItem value="Prueba">Prueba</MenuItem>
              <MenuItem value="Other">Other</MenuItem>
            </Select>
          </FormControl>
        </Grid>
      </Grid>
      <Grid container spacing={2}>
        <Grid item xs={6}>
          <BootstrapInput
            id="bootstrap-input"
            placeholder="Id Credit"
            value={credit}
            onChange={(e) => setCredit(e.target.value)}
            sx={{ width: "100%", marginBottom: "14px" }}
          />
        </Grid>
        <Grid item xs={6}>
          <BootstrapInput
            id="bootstrap-input"
            placeholder="Id Paid"
            value={paid}
            onChange={(e) => setPaid(e.target.value)}
            sx={{ width: "100%", marginBottom: "14px" }}
          />
        </Grid>
      </Grid>
      <Button variant="contained" onClick={submit}>
        SAVE
      </Button>
    </div>
  );
}
