import React, { useState, useEffect, useContext } from "react";
import Grid from "@mui/material/Grid";
import { useSelector } from "react-redux";
import LocalPhoneIcon from "@mui/icons-material/LocalPhone";
import Axios from "axios";
import { useHistory, useParams } from "react-router-dom";
import UserContext from "../../context/UserContext";
import "./index.css";

export default function Confirm() {
  let { id } = useParams();
  const history = useHistory();
  const { loginStatus, setLoginStatus } = useContext(UserContext);
  const [userInfo, setUserInfo] = useState({
    name: "",
    doc: "",
  });
  let customerId = useSelector((state) => state.email.customerId);

  useEffect(() => {
    if (!loginStatus) {
      history.push("/");
      return;
    }

    // Get customer information from "customer" table.
    Axios.get("http://localhost:5000/customers/get/" + customerId).then(
      async (response) => {
        setUserInfo({
          ...userInfo,
          ["name"]: response.data[0].name,
          ["doc"]: response.data[0].doc,
        });
      }
    );
  }, []);

  const transList = () => {
    history.push("/translist");
  };
  return (
    <div className="confirm">
      <Grid
        container
        justifyContent="center"
        alignItems="center"
        direction="column"
      >
        <i class="fas fa-check-circle"></i>
        <p className="username">
          {userInfo["name"] ? userInfo["name"] : <>Invited</>}
        </p>
        <p className="p-amount">{id}</p>
      </Grid>
      <Grid
        container
        justifyContent="space-between"
        alignItems="center"
        direction="row"
        sx={{ padding: "80px 20px" }}
      >
        <i onClick={transList} class="fas fa-home"></i>
        <i class="fas fa-share-alt"></i>
        <i class="fas fa-phone fa-rotate-90"></i>
        <i class="fas fa-print"></i>
      </Grid>
    </div>
  );
}
