import React, { useEffect, useState, useContext } from "react";
import TextField from "@mui/material/TextField";
import InputBase from "@mui/material/InputBase";
import { styled } from "@mui/material/styles";
import Button from "@mui/material/Button";
import UserContext from "../../context/UserContext";
import { useHistory } from "react-router-dom";
import { useSelector } from "react-redux";
import Axios from "axios";
import { useToasts } from "react-toast-notifications";
import RadioGroup from "@mui/material/RadioGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import Grid from "@mui/material/Grid";
import Radio from "@mui/material/Radio";
import "./index.css";

const BootstrapInput = styled(InputBase)(({ theme }) => ({
  "label + &": {
    marginTop: theme.spacing(3),
  },
  "& .MuiInputBase-input": {
    position: "relative",
    backgroundColor: "white",
    border: "1px solid #D9D9D6",
    fontSize: 14,
    width: "100%",
    padding: "12px 16px",
    transition: theme.transitions.create([
      "border-color",
      "background-color",
      "box-shadow",
      "border-width",
    ]),
    "&:focus": {
      borderColor: "#FA4616",
      borderWidth: "1px",
    },
  },
}));
export default function Invite() {
  const [date, setDate] = React.useState(null);
  const { loginStatus, setLoginStatus } = useContext(UserContext);
  const { addToast } = useToasts();
  const [invoiceInfo, setInvoiceInfo] = useState({
    amount: "",
    description: "",
    name: "Invited",
    doc: "",
    sellerId: "",
    customerId: "",
    type: "",
  });

  const history = useHistory();
  let email = useSelector((state) => state.email.currentEmail);

  useEffect(() => {
    if (!loginStatus) {
      history.push("/");
      return;
    }

    const id = email;
    Axios.get("http://localhost:5000/users/getId/" + id).then(
      async (response) => {
        setInvoiceInfo({
          ...invoiceInfo,
          ["sellerId"]: response.data._id,
        });
      }
    );
  }, []);

  const logout = () => {
    setLoginStatus(false);
    history.push("/");
    return;
  };

  const submit = () => {
    if (!invoiceInfo["amount"]) {
      addToast("Please Enter Amount", {
        appearance: "error",
        autoDismiss: true,
      });
      return;
    }

    if (!invoiceInfo["description"]) {
      addToast("Please Enter Description", {
        appearance: "error",
        autoDismiss: true,
      });
      return;
    }

    if (!invoiceInfo["type"]) {
      addToast("Please Select Type", {
        appearance: "error",
        autoDismiss: true,
      });
      return;
    }

    Axios.post("http://localhost:5000/trans/create", invoiceInfo).then(
      async (response) => {
        if (response.data.status === 200) {
          history.push({
            pathname: "/confirm/" + invoiceInfo["amount"],
          });
        }
      }
    );
  };

  // Go to previous page.
  const prevPage = () => {
    history.goBack();
  };
  return (
    <div className="invite">
      <div className="top">
        <p>New Invoice</p>
        <i class="fas fa-sign-out-alt" onClick={logout}></i>
      </div>
      <i class="fas fa-arrow-circle-left" onClick={prevPage}></i>
      <p className="customer">Invited</p>
      <div className="item">
        <p>Value</p>
        <div>
          <i class="fas fa-hand-holding-usd"></i>
          <BootstrapInput
            id="bootstrap-input"
            name="amount"
            placeholder="Enter Value"
            type="number"
            sx={{ width: "100%" }}
            onChange={(e) =>
              setInvoiceInfo({
                ...invoiceInfo,
                [e.target.name]: e.target.value,
              })
            }
          />
        </div>
      </div>
      <div className="item">
        <p>Description</p>
        <div>
          <i class="fas fa-pen-nib"></i>
          <BootstrapInput
            id="bootstrap-input"
            placeholder="Enter Description"
            name="description"
            sx={{ width: "100%" }}
            onChange={(e) =>
              setInvoiceInfo({
                ...invoiceInfo,
                [e.target.name]: e.target.value,
              })
            }
          />
        </div>
      </div>

      <Grid container justifyContent="center">
        <RadioGroup row aria-label="gender" name="row-radio-buttons-group">
          <FormControlLabel
            name="type"
            value="invoice"
            control={<Radio />}
            label="Invoice"
            onChange={(e) =>
              setInvoiceInfo({
                ...invoiceInfo,
                [e.target.name]: e.target.value,
              })
            }
          />
          <FormControlLabel
            name="type"
            value="ticket"
            control={<Radio />}
            label="Ticket"
            onChange={(e) =>
              setInvoiceInfo({
                ...invoiceInfo,
                [e.target.name]: e.target.value,
              })
            }
          />
        </RadioGroup>
      </Grid>
      <Button variant="contained" onClick={submit}>
        Send
      </Button>
    </div>
  );
}
