import React, { useState, useEffect, useContext } from "react";
import Axios from "axios";
import { useSelector } from "react-redux";
import UserContext from "../../context/UserContext";
import { useHistory } from "react-router-dom";
import Moment from "react-moment";
import { useTheme } from "@mui/material/styles";
import Button from "@mui/material/Button";
import Grid from "@mui/material/Grid";
import logoImg from "../../images/logo.png";
import "bootstrap/dist/css/bootstrap.min.css";
import { Dropdown } from "react-bootstrap";
import "./index.css";

export default function TransList() {
  const [tranData, setTranData] = useState([]);
  const { loginStatus, setLoginStatus } = useContext(UserContext);
  const [sellerId, setSellerId] = useState([]);
  const email = useSelector((state) => state.email.currentEmail);
  const [totalAmount, setTotalAmount] = useState(0);
  const history = useHistory();

  useEffect(() => {
    if (!loginStatus) {
      history.push("/");
      return;
    }

    Axios.get("http://localhost:5000/users/getId/" + email).then(
      async (response) => {
        setSellerId(response.data);
      }
    );
  }, []);

  useEffect(() => {
    const userInfo = Axios.get(
      "http://localhost:5000/trans/all/" + sellerId["_id"]
    ).then(async (response) => {
      const currentDate = new Date();
      const curFormatDate = currentDate.toISOString().slice(0, 10);

      var totalAmount = 0;
      for (var i = 0; i < response.data.length; i++) {
        const tranFormatDate = new Date(response.data[i].date)
          .toISOString()
          .slice(0, 10);
        if (curFormatDate === tranFormatDate) {
          totalAmount += response.data[i].amount;
        }
      }
      setTotalAmount(totalAmount);
      setTranData(response.data);
    });
  }, [sellerId]);

  const goCustomer = () => {
    history.push("/customer");
  };

  const logout = () => {
    setLoginStatus(false);
    history.push("/");
    return;
  };
  return (
    <div className="translist">
      <Grid className="top-info" container direction="column">
        <Grid container justifyContent="space-between">
          <img src={logoImg} alt="logo" />
          <Grid container direction="column">
            <p>{sellerId["name"]}</p>
            <p>RUC: {sellerId["ruc"]}</p>
          </Grid>
          {/* <i class="fas fa-bars"></i> */}
          <Dropdown>
            <Dropdown.Toggle sx={{ background: "unset" }}>
              <i class="fas fa-bars"></i>
            </Dropdown.Toggle>

            <Dropdown.Menu>
              <Dropdown.Item onClick={logout}>
                <i
                  class="fas fa-sign-out-alt fa-rotate-180"
                  style={{ marginRight: "20px" }}
                ></i>
                Log out
              </Dropdown.Item>
            </Dropdown.Menu>
          </Dropdown>
        </Grid>
        <Grid className="sale-amount" container justifyContent="space-between">
          <p>TODAY</p>
          <p>
            S/<span>{totalAmount}</span>
          </p>
        </Grid>
      </Grid>
      <div className="list">
        {tranData.map((row, index) => (
          <Grid
            direction="row"
            container
            alignItems="center"
            justifyContent="space-between"
            sx={{
              padding: "15px 0px",
              borderTop: "1px solid gray",
            }}
          >
            <Grid container direction="column">
              <p>{row["name"]}</p>
              <p>
                {row["type"]}&nbsp;/&nbsp;
                <Moment format="Do MMMM YYYY - HH:mm">{row["date"]}</Moment>
              </p>
            </Grid>
            <Grid>
              <p>S/&nbsp;&nbsp;{row["amount"]}</p>
            </Grid>
          </Grid>
        ))}
      </div>
      <Button
        variant="contained"
        sx={{ marginTop: "30px", marginBottom: "20px", width: "70%" }}
        onClick={goCustomer}
      >
        Send
      </Button>
    </div>
  );
}
