import React, { useState, useEffect, useContext } from "react";
import Axios from "axios";
import { Link } from "react-router-dom";
import { useToasts } from "react-toast-notifications";
import { useHistory } from "react-router-dom";
import UserContext from "../../context/UserContext";
import "./index.css";

export default function Admin() {
  const [users, setUsers] = useState([]);
  const { addToast } = useToasts();
  const { loginStatus, setLoginStatus } = useContext(UserContext);
  const history = useHistory();
  useEffect(() => {
    if (!loginStatus) {
      history.push("/");
    }

    Axios.get("http://localhost:5000/users/all").then((response) => {
      var users = [];
      for (let i = 0; i < response.data.length; i++) {
        var user = [];
        user = {
          id: response.data[i].id,
          name: response.data[i].name,
          ruc: response.data[i].ruc,
          branch: response.data[i].branch,
          vendor: response.data[i].vendor,
          process: response.data[i].process,
          credit: response.data[i].credit,
          paid: response.data[i].paid,
          token: response.data[i].token,
          email: response.data[i].email,
          active_status: response.data[i].active_status,
        };
        users.push(user);
      }
      setUsers(users);
    });
  }, [loginStatus]);

  const logout = () => {
    setLoginStatus(false);
  };

  const remove = (id) => {
    //Before clicking "Delete" button,
    var prevUsers = users;

    Axios.delete("http://localhost:5000/users/delete/" + id).then(
      (response) => {
        if (response.data.msg === "success") {
          var del_id = response.data.del_id;
          var userSize = Object.keys(prevUsers).length;
          var users = [];
          for (let i = 0; i < userSize; i++) {
            var user = [];
            if (del_id !== prevUsers[i].id) {
              user = {
                id: prevUsers[i].id,
                name: prevUsers[i].name,
                ruc: prevUsers[i].ruc,
                branch: prevUsers[i].branch,
                vendor: prevUsers[i].vendor,
                process: prevUsers[i].process,
                credit: prevUsers[i].credit,
                paid: prevUsers[i].paid,
                token: prevUsers[i].token,
                email: prevUsers[i].email,
                active_status: prevUsers[i].active_status,
              };
              users.push(user);
            }
          }
          setUsers(users);
          addToast("Deleted Successfully", {
            appearance: "success",
            autoDismiss: true,
          });
        } else {
          addToast("Sorry, Operation is failure", {
            appearance: "error",
            autoDismiss: true,
          });
        }
      }
    );
  };

  const deactive = (id) => {
    Axios.get("http://localhost:5000/users/deactive/" + id).then((response) => {
      var users = [];
      for (let i = 0; i < response.data.length; i++) {
        var user = [];
        user = {
          id: response.data[i].id,
          name: response.data[i].name,
          ruc: response.data[i].ruc,
          branch: response.data[i].branch,
          vendor: response.data[i].vendor,
          process: response.data[i].process,
          credit: response.data[i].credit,
          paid: response.data[i].paid,
          token: response.data[i].token,
          email: response.data[i].email,
          active_status: response.data[i].active_status,
        };
        users.push(user);
      }
      setUsers(users);
      addToast("Success, Deactivation", {
        appearance: "success",
        autoDismiss: true,
      });
    });
  };

  const active = (id) => {
    Axios.get("http://localhost:5000/users/active/" + id).then((response) => {
      var users = [];
      for (let i = 0; i < response.data.length; i++) {
        var user = [];
        user = {
          id: response.data[i].id,
          name: response.data[i].name,
          ruc: response.data[i].ruc,
          branch: response.data[i].branch,
          vendor: response.data[i].vendor,
          process: response.data[i].process,
          credit: response.data[i].credit,
          paid: response.data[i].paid,
          token: response.data[i].token,
          email: response.data[i].email,
          active_status: response.data[i].active_status,
        };
        users.push(user);
      }
      setUsers(users);
      addToast("Success, Activation", {
        appearance: "success",
        autoDismiss: true,
      });
    });
  };

  const edit = (id) => {
    history.push({
      pathname: "/admin/edit/" + id,
    });
  };

  const newUser = () => {
    history.push("/admin/new");
  };

  return (
    <div className="admin">
      <div>
        <i class="fas fa-sign-out-alt fa-rotate-180" onClick={logout}></i>
        <Link to="/invoice">Go Invoice</Link>
      </div>
      <i class="fas fa-user-plus" onClick={newUser}></i>
      <table class="table table-hover">
        <thead>
          <tr>
            <th>No</th>
            <th>Ruc</th>
            <th>Action</th>
          </tr>
        </thead>
        <tbody>
          {users.map((user, index) => (
            <tr key={index}>
              <td>{index + 1}</td>
              <td>{user.ruc}</td>
              <td>
                <i class="fas fa-pen" onClick={() => edit(user.id)}></i>
                <i class="fas fa-trash-alt" onClick={() => remove(user.id)}></i>
                {user.active_status ? (
                  <i class="fas fa-lock" onClick={() => deactive(user.id)}></i>
                ) : (
                  <i
                    class="fas fa-lock-open"
                    onClick={() => active(user.id)}
                  ></i>
                )}
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
