import React, { useState, useEffect, useContext } from "react";
import PropTypes from "prop-types";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import UserContext from "../../context/UserContext";
import { useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import Axios from "axios";
import InputBase from "@mui/material/InputBase";
import Grid from "@mui/material/Grid";
import { styled } from "@mui/material/styles";
import Button from "@mui/material/Button";
import { useToasts } from "react-toast-notifications";
import { useDispatch } from "react-redux";
//
import Paper from "@mui/material/Paper";
import Divider from "@mui/material/Divider";
import IconButton from "@mui/material/IconButton";
import MenuIcon from "@mui/icons-material/Menu";
import SearchIcon from "@mui/icons-material/Search";
import DirectionsIcon from "@mui/icons-material/Directions";
import * as Actions from "../../redux/action";
import "./index.css";

const BootstrapInput = styled(InputBase)(({ theme }) => ({
  "label + &": {
    marginTop: theme.spacing(3),
  },
  "& .MuiInputBase-input": {
    position: "relative",
    backgroundColor: "white",
    border: "1px solid #D9D9D6",
    fontSize: 14,
    width: "100%",
    padding: "12px 16px",
    transition: theme.transitions.create([
      "border-color",
      "background-color",
      "box-shadow",
      "border-width",
    ]),
    "&:focus": {
      borderColor: "#FA4616",
      borderWidth: "1px",
    },
  },
}));

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}

export default function Customer() {
  const dispatch = useDispatch();
  const history = useHistory();
  const [value, setValue] = React.useState(0);
  const { loginStatus, setLoginStatus } = useContext(UserContext);
  const { addToast } = useToasts();
  const [allCustomer, setAllCustomer] = useState([]);
  const [customerInfo, setCustomerInfo] = useState({
    sellerId: "",
    name: "",
    doc: "",
    direction: "",
  });
  let email = useSelector((state) => state.email.currentEmail);
  let id = "";
  useEffect(() => {
    if (!loginStatus) {
      history.push("/");
      return;
    }

    id = email;
    Axios.get("http://localhost:5000/users/getId/" + id).then(
      async (response) => {
        setCustomerInfo({
          ...customerInfo,
          ["sellerId"]: response.data._id,
        });
      }
    );
  }, []);

  useEffect(() => {
    // Get all customer of seller.
    id = customerInfo["sellerId"];
    Axios.get("http://localhost:5000/customers/all/" + id).then(
      async (response) => {
        setAllCustomer(response.data);
      }
    );
  }, [customerInfo["sellerId"]]);

  const handleChange = (event, newValue) => {
    setValue(newValue);

    // When user click "customer" tab.
    if (!newValue) {
      id = customerInfo["sellerId"];
      Axios.get("http://localhost:5000/customers/all/" + id).then(
        async (response) => {
          setAllCustomer(response.data);
        }
      );
    }
  };

  // When creating new customer.
  const saveCustomer = async () => {
    if (!customerInfo["doc"]) {
      addToast("Please Enter Doc", {
        appearance: "error",
        autoDismiss: true,
      });
      return;
    }
    if (!customerInfo["name"]) {
      addToast("Please Enter Name", {
        appearance: "error",
        autoDismiss: true,
      });
      return;
    }
    if (!customerInfo["direction"]) {
      addToast("Please Enter Direction", {
        appearance: "error",
        autoDismiss: true,
      });
      return;
    }

    const result = await Axios.post(
      "http://localhost:5000/customers/create",
      customerInfo
    );

    // Register Success Case
    if (result.data.status === 200) {
      addToast("Customer was created successfully", {
        appearance: "success",
        autoDismiss: true,
      });
      setCustomerInfo({
        ...customerInfo,
        ["doc"]: "",
        ["name"]: "",
        ["direction"]: "",
      });
    } else {
      addToast(result.data.msg, {
        appearance: "error",
        autoDismiss: true,
      });
    }
  };

  // When seller selects customer.
  const selectCustomer = (id) => {
    // Store current customer's ID to redux
    const payload = {
      email: email,
      customerId: id,
    };
    dispatch(Actions.saveEmail(payload));
    history.push("/invoice");
  };

  // Go to previous page.
  const prevPage = () => {
    history.goBack();
  };

  // Search functionality
  const originAllCustomer = allCustomer;
  const search = (e) => {
    // if search work doesn't exist,
    if (e.target.value) {
      const searchKey = e.target.value.toString().toLowerCase();
      let searchResult = [];
      for (var i = 0; i < allCustomer.length; i++) {
        if (
          allCustomer[i]["name"].toString().toLowerCase().includes(searchKey) ||
          allCustomer[i]["doc"].toString().toLowerCase().includes(searchKey)
        ) {
          searchResult.push(allCustomer[i]);
        }
      }
      setAllCustomer(searchResult);
    } else {
      Axios.get(
        "http://localhost:5000/customers/all/" + customerInfo["sellerId"]
      ).then(async (response) => {
        setAllCustomer(response.data);
      });
    }
  };

  // Create Invite Guest.
  const inviteGuest = () => {
    history.push("/invite");
  };
  return (
    <div className="customer">
      <i class="fas fa-arrow-circle-left" onClick={prevPage}></i>
      <Box sx={{ width: "100%" }}>
        <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
          <Tabs
            value={value}
            onChange={handleChange}
            aria-label="basic tabs example"
          >
            <Tab sx={{ width: "50%" }} label="Customer" {...a11yProps(0)} />
            <Tab sx={{ width: "50%" }} label="New Create" {...a11yProps(1)} />
          </Tabs>
        </Box>
        <TabPanel value={value} index={0}>
          <Paper
            component="form"
            sx={{
              p: "2px 4px",
              display: "flex",
              alignItems: "center",
              marginBottom: "30px",
              boxShadow: "unset",
              border: "1px solid #f5f5f5",
            }}
          >
            <InputBase
              sx={{ ml: 1, flex: 1 }}
              placeholder="Search Name or Doc"
              inputProps={{ "aria-label": "search google maps" }}
              onChange={(e) => search(e)}
            />
            <IconButton sx={{ p: "10px" }} aria-label="search">
              <SearchIcon />
            </IconButton>
          </Paper>
          {!allCustomer.length ? (
            <>No Customer</>
          ) : (
            allCustomer.map((cust, index) => (
              <Grid
                key={index}
                className="cust-list"
                sx={{
                  textAlign: "left",
                  borderTop: "1px solid #f5f5f5",
                  borderBottom: "1px soild #f5f5f5",
                }}
                onClick={() => selectCustomer(cust["id"])}
              >
                <p>{cust["name"]}</p>
                <p>{cust["doc"]}</p>
              </Grid>
            ))
          )}
          <Button
            variant="contained"
            sx={{ marginTop: "30px" }}
            onClick={inviteGuest}
            className="btn-action"
          >
            Invited
          </Button>
        </TabPanel>
        <TabPanel value={value} index={1}>
          <Grid container>
            <p>Doc</p>
            <BootstrapInput
              id="bootstrap-input"
              placeholder="Enter Doc"
              value={customerInfo["doc"]}
              name="description"
              sx={{ width: "100%" }}
              onChange={(e) => {
                setCustomerInfo({ ...customerInfo, ["doc"]: e.target.value });
              }}
            />
          </Grid>
          <Grid container>
            <p>Name</p>
            <BootstrapInput
              id="bootstrap-input"
              placeholder="Enter Name"
              name="description"
              sx={{ width: "100%" }}
              value={customerInfo["name"]}
              onChange={(e) => {
                setCustomerInfo({ ...customerInfo, ["name"]: e.target.value });
              }}
            />
          </Grid>
          <Grid container>
            <p>Direction</p>
            <BootstrapInput
              id="bootstrap-input"
              placeholder="Enter Direction"
              name="description"
              sx={{ width: "100%" }}
              value={customerInfo["direction"]}
              onChange={(e) => {
                setCustomerInfo({
                  ...customerInfo,
                  ["direction"]: e.target.value,
                });
              }}
            />
          </Grid>
          <Button
            variant="contained"
            sx={{ marginTop: "30px" }}
            onClick={saveCustomer}
            className="btn-action"
          >
            Save
          </Button>
        </TabPanel>
      </Box>
    </div>
  );
}
