import React, { useState, useEffect, useContext } from "react";
import { styled } from "@mui/material/styles";
import InputBase from "@mui/material/InputBase";
import FiberManualRecordIcon from "@mui/icons-material/FiberManualRecord";
import InputAdornment from "@mui/material/InputAdornment";
import OutlinedInput from "@mui/material/OutlinedInput";
import InputLabel from "@mui/material/InputLabel";
import FormControl from "@mui/material/FormControl";
import Visibility from "@mui/icons-material/Visibility";
import VisibilityOff from "@mui/icons-material/VisibilityOff";
import IconButton from "@mui/material/IconButton";
import Button from "@mui/material/Button";
import Axios from "axios";
import UserContext from "../../../context/UserContext";
import { useToasts } from "react-toast-notifications";
import { useHistory } from "react-router-dom";
import Grid from "@mui/material/Grid";
import Select from "@mui/material/Select";
import MenuItem from "@mui/material/MenuItem";
import "./index.css";

const BootstrapInput = styled(InputBase)(({ theme }) => ({
  "label + &": {
    marginTop: theme.spacing(3),
  },
  "& .MuiInputBase-input": {
    position: "relative",
    backgroundColor: "white",
    border: "1px solid #D9D9D6",
    fontSize: 14,
    width: "100%",
    padding: "12px 16px",
    transition: theme.transitions.create([
      "border-color",
      "background-color",
      "box-shadow",
      "border-width",
    ]),
    "&:focus": {
      borderColor: "#FA4616",
      borderWidth: "1px",
    },
  },
}));

export default function Create() {
  const [name, setName] = useState("");
  const [ruc, setRuc] = useState("");
  const [branch, setBranch] = useState("");
  const [token, setToken] = useState("");
  const [email, setEmail] = useState("");
  const [credit, setCredit] = useState("");
  const [vendor, setVendor] = useState("");
  const [process, setProcess] = useState("");
  const [paid, setPaid] = useState("");
  const [values, setValues] = React.useState({
    password: "",
    showPassword: false,
  });
  const [rePassword, setRePassword] = React.useState({
    repassword: "",
    showRePassword: false,
  });
  const { addToast } = useToasts();
  const { loginStatus, setLoginStatus } = useContext(UserContext);
  const history = useHistory();

  const handleChange = (prop) => (event) => {
    setValues({ ...values, [prop]: event.target.value });
  };

  const handleClickShowPassword = () => {
    setValues({
      ...values,
      showPassword: !values.showPassword,
    });
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };
  // RePassword
  const handleRePasswordChange = (prop) => (event) => {
    setRePassword({ ...rePassword, [prop]: event.target.value });
  };

  const handleClickShowRePassword = () => {
    setRePassword({
      ...rePassword,
      showRePassword: !rePassword.showRePassword,
    });
  };

  // Submit New User Information to database
  const submit = async () => {
    const password = values.password;
    const repassword = rePassword.repassword;
    if (!name) {
      addToast("Please Enter Name", {
        appearance: "error",
        autoDismiss: true,
      });
      return;
    }
    if (!ruc) {
      addToast("Please Enter Ruc", {
        appearance: "error",
        autoDismiss: true,
      });
      return;
    }
    if (!branch) {
      addToast("Please Enter Id Sucursal", {
        appearance: "error",
        autoDismiss: true,
      });
      return;
    }
    if (!token) {
      addToast("Please Enter Token", {
        appearance: "error",
        autoDismiss: true,
      });
      return;
    }
    if (!email) {
      addToast("Please Enter Email", {
        appearance: "error",
        autoDismiss: true,
      });
      return;
    }
    if (!vendor) {
      addToast("Please Enter Id Vendor", {
        appearance: "error",
        autoDismiss: true,
      });
      return;
    }
    if (!process) {
      addToast("Please Enter Tipo Proceso", {
        appearance: "error",
        autoDismiss: true,
      });
      return;
    }
    if (!credit) {
      addToast("Please Enter Credit", {
        appearance: "error",
        autoDismiss: true,
      });
      return;
    }
    if (!paid) {
      addToast("Please Enter Paid", {
        appearance: "error",
        autoDismiss: true,
      });
      return;
    }
    if (password !== repassword) {
      addToast("Please Confirm Password", {
        appearance: "error",
        autoDismiss: true,
      });
      return;
    }

    const newUser = {
      name,
      ruc,
      branch,
      token,
      email,
      vendor,
      process,
      credit,
      paid,
      password,
    };
    const resultRegister = await Axios.post(
      "http://localhost:5000/users/register",
      newUser
    );

    // Register Success Case
    if (resultRegister.data.status === 200) {
      history.push("/admin");
    } else {
      addToast(resultRegister.data.msg, {
        appearance: "error",
        autoDismiss: true,
      });
    }
  };
  return (
    <div className="register">
      <div className="top-bar"></div>
      <p className="title">Sign up</p>
      <div className="reg-item">
        <BootstrapInput
          id="bootstrap-input"
          placeholder="Name"
          value={name}
          onChange={(e) => setName(e.target.value)}
          sx={{ width: "100%", marginBottom: "14px" }}
        />
        <Grid container spacing={2}>
          <Grid item xs={6}>
            <BootstrapInput
              id="bootstrap-input"
              placeholder="Ruc"
              value={ruc}
              onChange={(e) => setRuc(e.target.value)}
              sx={{ width: "100%", marginBottom: "14px" }}
            />
          </Grid>
          <Grid item xs={6}>
            <BootstrapInput
              id="bootstrap-input"
              placeholder="Id Sucursal"
              value={branch}
              onChange={(e) => setBranch(e.target.value)}
              sx={{ width: "100%", marginBottom: "14px" }}
            />
          </Grid>
        </Grid>
        <BootstrapInput
          id="bootstrap-input"
          placeholder="Token"
          value={token}
          onChange={(e) => setToken(e.target.value)}
          sx={{ width: "100%", marginBottom: "14px" }}
        />
        <BootstrapInput
          id="bootstrap-input"
          placeholder="Email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          sx={{ width: "100%", marginBottom: "14px" }}
        />
        <Grid container spacing={2}>
          <Grid item xs={6}>
            <BootstrapInput
              id="bootstrap-input"
              placeholder="Id Vendor"
              value={vendor}
              onChange={(e) => setVendor(e.target.value)}
              sx={{ width: "100%", marginBottom: "14px" }}
            />
          </Grid>
          <Grid item xs={6}>
            <FormControl sx={{ minWidth: 120 }}>
              <InputLabel id="demo-simple-select-helper-label">
                Tipo Proceso
              </InputLabel>
              <Select
                value={process}
                onChange={(e) => setProcess(e.target.value)}
                displayEmpty
                inputProps={{ "aria-label": "Without label" }}
              >
                <MenuItem value="Prueba">Prueba</MenuItem>
                <MenuItem value="Other">Other</MenuItem>
              </Select>
            </FormControl>
          </Grid>
        </Grid>
        <Grid container spacing={2}>
          <Grid item xs={6}>
            <BootstrapInput
              id="bootstrap-input"
              placeholder="Id Credit"
              value={credit}
              onChange={(e) => setCredit(e.target.value)}
              sx={{ width: "100%", marginBottom: "14px" }}
            />
          </Grid>
          <Grid item xs={6}>
            <BootstrapInput
              id="bootstrap-input"
              placeholder="Id Paid"
              value={paid}
              onChange={(e) => setPaid(e.target.value)}
              sx={{ width: "100%", marginBottom: "14px" }}
            />
          </Grid>
        </Grid>

        <FormControl
          sx={{ width: "100%", marginBottom: "14px" }}
          variant="outlined"
        >
          <InputLabel htmlFor="outlined-adornment-password">
            Password
          </InputLabel>
          <OutlinedInput
            id="outlined-adornment-password"
            type={values.showPassword ? "text" : "password"}
            value={values.password}
            onChange={handleChange("password")}
            endAdornment={
              <InputAdornment position="end">
                <IconButton
                  aria-label="toggle password visibility"
                  onClick={handleClickShowPassword}
                  onMouseDown={handleMouseDownPassword}
                  edge="end"
                >
                  {values.showPassword ? <VisibilityOff /> : <Visibility />}
                </IconButton>
              </InputAdornment>
            }
            label="Password"
          />
        </FormControl>
        <FormControl
          sx={{ width: "100%", marginBottom: "14px" }}
          variant="outlined"
        >
          <InputLabel htmlFor="outlined-adornment-password">
            Confrim Password
          </InputLabel>
          <OutlinedInput
            id="outlined-adornment-password"
            type={rePassword.showRePassword ? "text" : "password"}
            value={rePassword.repassword}
            onChange={handleRePasswordChange("repassword")}
            endAdornment={
              <InputAdornment position="end">
                <IconButton
                  aria-label="toggle password visibility"
                  onClick={handleClickShowRePassword}
                  onMouseDown={handleMouseDownPassword}
                  edge="end"
                >
                  {rePassword.showRePassword ? (
                    <VisibilityOff />
                  ) : (
                    <Visibility />
                  )}
                </IconButton>
              </InputAdornment>
            }
            label="Password"
          />
        </FormControl>
        <Button variant="contained" onClick={submit}>
          Register
        </Button>
      </div>
    </div>
  );
}
